<template>
  <div class="add_list">
    <div class="add_list_search">
      <el-input size="medium" v-model="searchKey" placeholder="搜索货币" @input="searchInput">
          <template slot="prepend"><span style="color:rgb(102, 135, 242);font-size:23px;background-color:rgb(49,63,82);padding-left:0px">
             <el-button type="primary" icon="el-icon-search"></el-button>
              </span></template>
        </el-input>
        </div>
        <div class="add_list_country" style="margin-top:45px">
          <div class="add_list_country_item" v-for="(item,index) in add_list_country_good" :key="index" @click="changStatus(item)">
            <!-- <span><img :src="item.flagURL" alt="" width="45px"></span> -->
            <span><img :src="'assets/flags/4x3/'+item.tcur.slice(0,2).toLowerCase()+'.svg'" alt="" width="45px"></span>
            <span style="color:rgb(255,255,240);font-size:16px;justify-self:start;">{{item.name}}</span>
            <span><i class="el-icon-check" style="font-size:22px;color:rgb(102,135,250)" v-if="item.select===1"></i></span>
          </div>

        </div>
    <div class="add_country_button"><el-button type="danger" icon="el-icon-close" circle @click="go_list"></el-button></div>
  </div>
</template>


<script>
export default {
  data(){return{
    searchKey:'',  //搜索关键词
    add_list_country:[],  //所有显示列表
    my_rate_zz_countries:[], //货币代码
  }},
  computed:{
    add_list_country_good:function(){
      let c = []
      this.add_list_country.forEach(e=>{
        if(e.status==='ALREADY'){
          c.push(e)
        }
      })
      return c
    }
  },
  created(){
    this.resh_go()
  },
  methods:{
    go_list(){
      this.$router.push('/')
    },
  searchInput(){
    //console.log('搜索事件启动...')
    //console.log(key)
    let key = this.searchKey.trim().toUpperCase()
    let c = []
    if(key === ''){
          this.add_list_country.forEach(e=>{
              e.status = 'ALREADY'
          })
    }else{
          this.add_list_country.forEach(e=>{
          if(e.name.includes(key)){
          }else{
            e.status = 'noShow'
          }
        })
    }
  },
    changStatus(item){
          let self = this
          //console.log(item)
          let c = [] //国家代码新数组
          if(this.my_rate_zz_countries.includes(item.tcur)){
            this.my_rate_zz_countries.forEach(e=>{
              if(e === item.tcur){
              }else{
                c.push(e)
              }
            })
            this.my_rate_zz_countries = c
          }else{
            this.my_rate_zz_countries.push(item.tcur)
          }
          //this.my_rate_zz_countries.push(item.tcur)
          chrome.storage.local.set({ "my_rate_zz_countries": this.my_rate_zz_countries }, function (s) {  
          //console.log("追加国家成功,放入my_rate_zz_countries");
          if(self.searchKey===''){
            self.resh_go()
          }else{
            let f = []
            self.add_list_country.forEach(el=>{
              if(el.tcur === item.tcur){
                if(el.select===1){
                  el.select = 0
                }else{
                  el.select = 1
                }
                f.push(el)
              }else{
                f.push(el)
              }
            })
            self.add_list_country = f
          }
          
         
        });
    },
    resh_go(){
      let self = this
      chrome.storage.local.get(["my_rate_zz",'my_rate_zz_countries'],function(result){
        //console.log("从storage中取出来的汇率数据为:");
        //console.log(result);
        const {my_rate_zz,my_rate_zz_countries} = result
        self.my_rate_zz_countries = my_rate_zz_countries
        my_rate_zz_countries.forEach(el => {
          my_rate_zz[el].select = 1
        });
        let b = []
        let c = Object.keys(my_rate_zz)
        c.forEach(e=>{
          b.push(my_rate_zz[e])
        })
        self.add_list_country = b
    });
    }
  }
}
</script>

<style >
.add_list{
  margin-top:6px;
}
.add_list .el-button--primary{
  padding:0;
  margin: 0;
}
.add_list .el-input-group__prepend{
  background-color:rgb(49,63,82);
  border:0;
  padding-left:0 10px 0 10px;
}
.add_list .el-input__inner{
  background-color:rgb(49,63,82);
  border-top-style: none;
  border-left-style: none;
  border-right-style: none;
  border-bottom-style: none;
  font-size:18px;
  color:rgb(255, 255, 230);
}
.add_list_country_item{
  width: 100%;
  height: 58px;
  display: grid;
  grid-template-columns: 65px 180px auto;
  place-items: center center;
  border-bottom:1px solid rgb(49,63,82)
}
.add_list_country_item:hover{
  background-color: rgb(53, 75, 105);
}
.add_list_search{
  margin-top:4px;
  height: 40px;
  position:fixed;
  width: 100%;
  top:0px;
  z-index: 1200;
}
</style>